import { Component, OnInit ,Input} from '@angular/core';

@Component({
  selector: 'app-status',
  templateUrl: './status.component.html',
  styleUrls: ['./status.component.css']
})
export class StatusComponent implements OnInit {
 
  showLog : string;

  constructor() { 
    this.showLog = "Generate Started";
  }

  ngOnInit() {
    
  }

  showStatus(){
    setTimeout(()=>{
      let logmsg : string = 'Generate Started ... <br> Directory created..<br>Generating package ... done</br>Package generated... done';
      this.showLog = logmsg;
      console.log(this.showLog)
    },1000)
  }

}



