import { Component, OnInit, OnDestroy, OnChanges,Input, AfterViewInit, ViewChild } from '@angular/core';
//Import output interface 
import { Output } from "../output";

import { StatusComponent } from '../status/status.component';

@Component({
  selector: 'app-output',
  templateUrl: './output.component.html',
  styleUrls: ['./output.component.css']
})
export class OutputComponent implements AfterViewInit {

  @Input() outputlogger : Output;
  @Input() viewState    : boolean;
  showStatus:boolean;
  @ViewChild(StatusComponent)
  public triggerChildComponentMethod : StatusComponent;

  constructor() { 
        this.showStatus = false;     
   }

  ngAfterViewInit() {
    
  }

  ngOnDestroy(){
    
  }

  ngOnChanges(changes){
     this.changeCheck();
  }

  changeCheck(){
    if(this.viewState == true){
      this.showStatus=false;
    }
  }

  generatePackage(){
    this.showStatus = true;
    setTimeout(()=>{
      this.triggerChildComponentMethod.showStatus();
    },2000)
   
  }

  triggerchildMethod() {
    
  }


 

}



