import { Component, OnInit } from '@angular/core';
//import model
import { Model } from '../model'
//import output service 
import { OutputService } from "../output.service";
//Import output interface 
import { Output } from "../output";


@Component({
  selector: 'app-input',
  templateUrl: './input.component.html',
  styleUrls: ['./input.component.css']
})
export class InputComponent implements OnInit {
  
    logData:Output[];
    //links hide/show
    firstlink  : boolean;
    secondlink : boolean;
    thirdlink  : boolean;
    fourthlink : boolean;
    viewStatus : boolean;
    model = new Model();

  constructor(private packagelog : OutputService) {
    this.firstlink  = false;
    this.secondlink = false;
    this.thirdlink  = false;
    this.fourthlink = false;
    this.viewStatus = false;
  }

  ngOnInit() {
    console.log(this.model);
  }

  installincludes_path(type){
      if(type==1){
        this.firstlink = true;
      } else if(type == 0){
        this.firstlink = false;
      }
    
  }
  zipincludes_path(type){
      if(type ==1){
        this.secondlink = true;
      } else if(type == 0){
        this.secondlink = false;
      }
      
  }
  package_path(type){
    console.log("triggered common");
      if(type ==1){
        this.thirdlink = true;
      } else if(type == 0){
        console.log("triggered else part");
        this.thirdlink = false;
      }
      
  }
  source_folder(type){
      if(type == 1){
        this.fourthlink = true;
      } else if(type == 0){
        this.fourthlink = false;
      }
  }

  triggerOutput(value){
    if(value=="./done"){
      this.packagelog.getStubData().then(data => { this.logData = data;console.log(data);});
      this.viewStatus = true;
    } else {
      this.logData = [];
      this.viewStatus = false;
    }
  }

}



